import React from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";

import Home from "./components/pages/Home/index";
import Preferences from "./components/pages/Preferences/index";
import Food from "./components/pages/Food/index";
import NavTabs from "./components/Navbar/index";
import SignUp from "./components/pages/SignUp/index";
import LogIn from "./components/pages/Login/index";
// import "../src/components/pages/style.css"


function App() {
  return (
   
    <Router basename={process.env.PUBLIC_URL}>
      <div>
        {/* <NavTabs /> */}
        <Route exact path={process.env.PUBLIC_URL + "/"} component={Home} />
        <Route exact path={process.env.PUBLIC_URL + "/preferences"} component={Preferences} />
        <Route exact path={process.env.PUBLIC_URL + "/food"} component={Food} />
        <Route exact path={process.env.PUBLIC_URL + "/signup"} component={SignUp} />
        <Route exact path={process.env.PUBLIC_URL + "/login"} component={LogIn} />
 
      </div>
    </Router>
  );
}

export default App;
